<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vuejs-accessibility/click-events-have-key-events -->
<template>
  <main v-if="productLoading" class="loader"></main>
  <main class="content container" v-else-if="!productData">
    Ошибка загрузки товара
    <button class="button button--primery" @click="loadProduct">
      Попробовать еще раз
    </button>
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main', params: {categoryId: category.id}}">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570"
            :src="product.colors[colorIndex].gallery
              ? product.colors[colorIndex].gallery[0].file.url
              : 'img/no-img.png'"
            :alt="product.title"
          >
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="(color, index) in product.colors" :key="index">
            <a href="" class="pics__link"
              :class="{'pics__link--current': colorIndex === index}"
              @click.prevent="colorIndex = index; checkedColorId = color.color.id"
            >
              <img width="98" height="98"
                :src="product.colors[index].gallery
                  ? product.colors[index].gallery[0].file.url
                  : 'img/no-img.png'"
                :alt="product.title"
              >
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <div class="item__row item__row--center">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click="dedAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount" name="count">

                <button type="button" aria-label="Добавить один товар" @click="addAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="item__price">
                {{ product.price | numberFormat }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li class="colors__item" v-for="(color, index) in product.colors" :key="color.id">
                    <label class="colors__label">
                      <input class="colors__radio sr-only" type="radio"
                        @click="colorIndex = index"
                        :value="color.color.id"
                        v-model="checkedColorId"
                      >
                      <span class="colors__value" :style="{backgroundColor: color.color.code}">
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" type="text" name="category" v-model.number="checkedSizeId">
                    <option v-for="size in product.sizes" :key="size.id" :value="size.id">{{ size.title }}</option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" type="submit" :disabled="productAddSending">
              В корзину
            </button>

            <div v-show="productAdded">Товар добавлен в корзину</div>
            <div v-show="productAddSending">Добавляем товар в корзину...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item" v-for="tab in tabs" :key="tab.name" @click.prevent="currentTab = tab.name">
            <a href="#" class="tabs__link" :class="{'tabs__link--current': currentTab === tab.name}">
              {{ tab.title }}
            </a>
          </li>
        </ul>
        <component :is="currentTab"></component>
      </div>

    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import numberFormat from '@/helpers/numberFormat';
import { RouterLink } from 'vue-router';
import TabInfoProduct from '@/components/TabInfoProduct.vue';
import TabDeliveryProduct from '@/components/TabDeliveryProduct.vue';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      currentTab: 'TabInfoProduct',
      tabs: [
        { name: 'TabInfoProduct', title: 'Информация о товаре' },
        { name: 'TabDeliveryProduct', title: 'Доставка и возврат' },
      ],

      colorIndex: 0,

      productAmount: 1,
      checkedColorId: null,
      checkedSizeId: null,

      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),

    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
          .then((response) => { this.productData = response.data; })
          .then(() => {
            this.checkedColorId = this.productData.colors[0].color.id;
            this.checkedSizeId = this.productData.sizes[0].id;
          })
          .catch(() => { this.productLoadingFailed = true; })
          .then(() => { this.productLoading = false; });
      }, 500);
    },
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        colorId: this.checkedColorId,
        sizeId: this.checkedSizeId,
        amount: this.productAmount,
      })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    addAmount() {
      this.productAmount += 1;
    },
    dedAmount() {
      if (this.productAmount > 1) this.productAmount -= 1;
    },
  },
  components: { RouterLink, TabInfoProduct, TabDeliveryProduct },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
