<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'main'}" class="breadcrumbs__link" href="#">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'cart'}" class="breadcrumbs__link" href="#">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <div v-if="orderSending" class="loader"></div>
      <form v-if="!orderSending" class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText type="text" title="ФИО" :error="formError.name" placeholder="Введите ваше полное имя" v-model="formData.name" />
            <BaseFormText type="text" title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес" v-model="formData.address" />
            <BaseFormText type="tel" title="Телефон" :error="formError.phone" placeholder="Введите ваш телефон" v-model="formData.phone" />
            <BaseFormText type="email" title="Email" :error="formError.email" placeholder="Введи ваш Email" v-model="formData.email" />
            <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment" placeholder="Ваши пожелания" v-model="formData.comment" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="delivery in deliveriesData" :key="delivery.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" :value="delivery.id" v-model="deliveryTypeId" :checked="true">
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ delivery.price | numberFormat }} ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="payment in paymentsData" :key="payment.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" :value="payment.id" v-model="paymentTypeId" :checked="true">
                  <span class="options__value">
                    {{ payment.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" v-for="item in products" :key="String(item.productId) + item.colorId + item.sizeId">
            <li class="cart__order">
              <h3>{{ item.product.product.title }}</h3>
              <b>{{ item.product.price * item.amount | numberFormat }} ₽</b>
              <span>Количество: {{ item.amount }}</span>
              <span>
                Цвет:
                {{ item.product.color.color.title }}
              </span>
              <span>Артикул: {{ String(item.productId) + item.colorId + item.sizeId }}</span>
              <span>
                Размер:
                {{ item.product.size.title }}
              </span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ priceDelivery | numberFormat }} ₽</b></p>
            <p>Итого: <b>{{ totalAmount }}</b> товара на сумму <b>{{ Number(totalprice) + Number(priceDelivery) | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { RouterLink } from 'vue-router';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',

      orderSending: false,

      deliveriesData: null,
      deliveryTypeId: null,
      paymentsData: null,
      paymentTypeId: null,
    };
  },
  components: { BaseFormText, BaseFormTextarea, RouterLink },
  filters: { numberFormat },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalprice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),
    priceDelivery() {
      let priceDelivery = 0;
      if (this.deliveriesData && this.deliveryTypeId) {
        priceDelivery = this.deliveriesData.find((delivery) => (
          delivery.id === this.deliveryTypeId
        )).price;
      }
      return priceDelivery;
    },
  },
  methods: {
    loadDeliveries() {
      axios.get(`${API_BASE_URL}/api/deliveries`)
        .then((response) => { this.deliveriesData = response.data; })
        .then(() => { this.deliveryTypeId = this.deliveriesData[0].id; });
    },
    loadPayments() {
      if (this.deliveryTypeId) {
        axios.get(`${API_BASE_URL}/api/payments`, {
          params: {
            deliveryTypeId: this.deliveryTypeId,
          },
        })
          .then((response) => { this.paymentsData = response.data; })
          .then(() => { this.paymentTypeId = this.paymentsData[0].id; });
      }
    },
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderSending = true;
      clearTimeout(this.sendingOrderTimer);
      this.sendingOrderTimer = setTimeout(() => {
        axios.post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
          deliveryTypeId: this.deliveryTypeId,
          paymentTypeId: this.paymentTypeId,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
          .then((response) => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => { this.orderSending = false; });
      }, 500);
    },
  },
  created() {
    this.loadDeliveries();
  },
  watch: {
    deliveryTypeId: {
      handler() {
        this.loadPayments();
      },
      immediate: true,
    },
  },
};
</script>
