<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          Товаров в каталоге - {{ countProducts }}
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :materials-ids.sync="filterMaterialsIds"
        :seasons-ids.sync="filterSeasonsIds"
      />
      <section class="catalog">
        <div v-if="productsLoading" class="loader"></div>
        <div v-if="productsLoadingFailed">Ошибка загрузки товаров <button class="button button--primery" @click="loadProducts">Попробовать еще раз</button></div>
        <ProductList v-if="!productsLoading" :products="products" />
        <BasePagination v-if="!productsLoading" v-model="page" :count="countProducts" :per-page="productsPerPages" />
      </section>

    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: this.$route.params.categoryId ? this.$route.params.categoryId : 0,
      filterMaterialsIds: [],
      filterSeasonsIds: [],

      page: 1,
      productsPerPages: 12,

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          img: product.colors[0].gallery ? product.colors[0].gallery[0].file.url : 'img/no-img.png',
        }))
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPages,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            categoryId: this.filterCategoryId,
            materialIds: this.filterMaterialsIds,
            seasonIds: this.filterSeasonsIds,
          },
        })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 500);
    },
  },
  created() {
    this.loadProducts();
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterMaterialsIds() {
      this.loadProducts();
    },
    filterSeasonsIds() {
      this.loadProducts();
    },
  },
};
</script>

<style>
  .loader,
  .loader:after {
    overflow:hidden;
    border-radius: 50%;
    width: 10em;
    height: 10em;
  }

  .loader {
    margin: 60px auto;
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    border-top: 1.1em solid rgba(101,171,248, 0.2);
    border-right: 1.1em solid rgba(101,171,248, 0.2);
    border-bottom: 1.1em solid rgba(101,171,248, 0.2);
    border-left: 1.1em solid #65abf8;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation: load8 1.1s infinite linear;
    animation: load8 1.1s infinite linear;
  }

  @-webkit-keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes load8 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
</style>
