import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
    isCartLoading: false,
  },
  mutations: {
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    updateCartProductAmount(state, payload) {
      const item = state.cartProducts.find((product) => (
        product.productId === payload.productId
        && product.colorId === payload.colorId
        && product.sizeId === payload.sizeId
      ));

      if (item) {
        item.amount = payload.amount;
      }
    },
    deleteCartProduct(state, { productId, colorId, sizeId }) {
      state.cartProducts = state.cartProducts.filter((item) => (
        item.productId !== productId
        || item.colorId !== colorId
        || item.sizeId !== sizeId
      ));
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        colorId: item.color.color.id,
        sizeId: item.size.id,
        amount: item.quantity,
      }));
    },
    updateIsCartLoading(state, bool) {
      state.isCartLoading = bool;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find((productData) => (
          productData.product.id === item.productId
          && productData.color.color.id === item.colorId
          && productData.size.id === item.sizeId
        ));
        return {
          ...item,
          product: {
            ...product,
            img: product.color.gallery ? product.color.gallery[0].file.url : 'img/no-img.png',
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      // eslint-disable-next-line
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    cartTotalAmount(state, getters) {
      // eslint-disable-next-line
      return getters.cartDetailProducts.reduce((acc, item) => item.amount + acc, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      context.commit('updateIsCartLoading', true);
      clearTimeout(this.loadBasketsTimer);
      this.loadBasketsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((response) => {
            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccessKey', response.data.user.accessKey);
              context.commit('updateUserAccessKey', response.data.user.accessKey);
            }
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
          })
          .then(() => { context.commit('updateIsCartLoading', false); });
      }, 500);
    },
    addProductToCart(context, {
      productId,
      colorId,
      sizeId,
      amount,
    }) {
      return axios.post(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        colorId,
        sizeId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount(context, {
      productId,
      colorId,
      sizeId,
      amount,
    }) {
      context.commit('updateCartProductAmount', {
        productId,
        colorId,
        sizeId,
        amount,
      });
      if (amount < 1) {
        return {};
      }
      const product = context.state.cartProductsData.find((productData) => (
        productData.product.id === productId
        && productData.color.color.id === colorId
        && productData.size.id === sizeId
      ));
      return axios.put(`${API_BASE_URL}/api/baskets/products`, {
        basketItemId: product.id,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProduct(context, { productId, colorId, sizeId }) {
      context.commit('deleteCartProduct', { productId, colorId, sizeId });
      const product = context.state.cartProductsData.find((productData) => (
        productData.product.id === productId
        && productData.color.color.id === colorId
        && productData.size.id === sizeId
      ));
      return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
        data: {
          basketItemId: product.id,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
  },
});
